<template>
  <div class="table">
    <!-- 条件筛选 -->
    <el-row class='table-head'>
      <el-col :span='16'>
        <label>用户名：</label>
        <el-input style='width:120px;' size='mini'></el-input>
        <label>状态：</label>
        <el-select v-model="value" placeholder="请选择" size='mini' style='width:120px;'>
          <el-option
            key="1"
            label="禁用"
            value="1">
          </el-option>
        </el-select>
        <el-button size='mini' type='primary'>查找</el-button>
      </el-col>
      <!-- <el-col :span='8' class='table-head-btn'>
        <el-button size='mini' type='success' @click='changeshow'>新增管理员</el-button>
        <el-button size='mini' type='default'>导出Exel表格</el-button> -->
      <!-- </el-col>  -->
    </el-row>
    <!-- 列表 -->
    <el-table
      :data="tableData.list"
      stripe
      style="width: 100%">
      <el-table-column
        prop="id"
        width='100'
        label="ID">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="手机号">
      </el-table-column>
      <el-table-column label='状态'>
        <template slot-scope="scope">
          {{scope.row.status === 1 ? '启用' : '停用'}}
        </template>
      </el-table-column>
      <el-table-column label='类型'>
        <template slot-scope="scope">
          {{scope.row.type === 1 ? '管理员' : '普通用户'}}
        </template>
      </el-table-column>
      <el-table-column
        label="注册时间" 
        width="200">
        <template slot-scope="scope">
          {{getDate(scope.row.meta.regTime)}}
        </template>
      </el-table-column>
      <el-table-column
        width="300"
        label="操作">
        <template slot-scope="scope">
          <el-button @click='changStatus(scope.row.id, 1)' v-if='scope.row.status === 0' type='success' size='mini'>启用</el-button>
          <el-button @click='changStatus(scope.row.id, 0)' v-if='scope.row.status === 1' type='warning' size='mini'>停用</el-button>
          <el-button type='primary' size='mini' @click='changeshow(scope.row.id, true)'>编辑信息</el-button>
          <!-- <el-button type='default' size='mini'>密码重置</el-button> -->
          <el-button type='danger' size='mini' @click='removeUser(scope.row.id)'>删除用户</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <div class="table-page">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="tableData.count">
      </el-pagination>
    </div>
    <!-- 弹出层 -->
    <transition name="fade">
      <userInfo :userId='selectId' v-if='show' @close='changeshow(false)'></userInfo>
    </transition>
  </div>
</template>

<script>
import userInfo from './userInfo';
import { getUserList, changeUserStatus, removeUser } from '@/js/http'; 
import axios from 'axios';
function getCookie(name){
  var strcookie = document.cookie;//获取cookie字符串
  var arrcookie = strcookie.split("; ");//分割
  //遍历匹配
  for ( var i = 0; i < arrcookie.length; i++) {
    var arr = arrcookie[i].split("=");
    if (arr[0] == name){
      return arr[1];
    }
  }
  return "";
}

export default {
  components: { userInfo },
  mounted() {
    this.getList();
  },
  data() {
    return {
      show: false,
      value:'1',
      tableData:[],
      selectId: 0,
    }
  },
  methods: {
    async getList() { // 获取用户列表
      
      const res = await getUserList();

      // const res = await axios.get('http://127.0.0.1:7001/v1/admin/getUserList',{withCredentials: true})
      this.tableData = res.data.data;

    },
    getDate(str) { // 格式化日期

      let date = new Date(str);
      date = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
      return date;

    },
    changStatus(id, status) { // 更改用户状态

      this.$confirm('您将更改该用户的状态，是否确认？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const res = await changeUserStatus({ id, status });
        this.$message({
          type: 'success',
          message: '更改用户状态成功!'
        });
        this.getList();
      });

    },
    removeUser(id) {
      this.$prompt('请输入管理员密码再删除用户', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async (password) => {
        const res = await removeUser({ id, password });
        console.log(res);
      })
    },
    changeshow(id, val) {
      if(val) {
        this.selectId = id;
      }
      this.show = val;
    },
  },
}
</script>

<style lang='scss' scoped>
.table {
  &-head {
    padding: 10px 0 30px 0;
    label {
      font-size: 14px;
      color: #999;
    }
    &-btn {
      text-align: right;
    }
    .el-input {
      margin-right: 20px;
    }
    .el-select {
      margin-right: 20px;
    }
  }
  &-page {
    padding-top: 30px;
  }
}
</style>
