<template>
  <div class="data">
    <el-breadcrumb>
      <el-breadcrumb-item>数据展示</el-breadcrumb-item>
      <el-breadcrumb-item>网站数据</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row class='data-info' :gutter='20'>
      <el-col :span='6' class='data-info-box'>
        <div class="box box1">
          <div class="number">123456</div>
          <div class="desc">注册人数</div>
        </div>
      </el-col>
      <el-col :span='6' class='data-info-box'>
        <div class="box box2">
          <div class="number">123456</div>
          <div class="desc">注册人数</div>
        </div>
      </el-col>
      <el-col :span='6' class='data-info-box'>
        <div class="box box3">
          <div class="number">123456</div>
          <div class="desc">注册人数</div>
        </div>
      </el-col>
      <el-col :span='6' class='data-info-box'>
        <div class="box box4">
          <div class="number">123456</div>
          <div class="desc">注册人数</div>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter='20'>
      <el-col :span='12'>
        <div class="data-chart1">
          <div class="data-chart1-title">本月注册用户表</div>
          <div id="c1"></div>
        </div>
      </el-col>
      <el-col :span='12'>
        <div class="data-chart1">
          <div class="data-chart1-title">本月活跃用户表</div>
          <div id="c2"></div>
        </div>
      </el-col>
    </el-row>
    
  </div>
</template>

<script>
export default {
  mounted() {
    let width = document.getElementById('c1').offsetWidth
    const data = [
        { genre: '05/01', sold: 275 },
        { genre: '05/02', sold: 115 },
        { genre: '05/03', sold: 120 },
        { genre: '05/04', sold: 200 },
        { genre: '05/05', sold: 150 }
      ]; // G2 对数据源格式的要求，仅仅是 JSON 数组，数组的每个元素是一个标准 JSON 对象。
      // Step 1: 创建 Chart 对象
      const chart = new G2.Chart({
        container: 'c1', // 指定图表容器 ID
        width, // 指定图表宽度
        height : 400 // 指定图表高度
      });
      const chart1 = new G2.Chart({
        container: 'c2', // 指定图表容器 ID
        width, // 指定图表宽度
        height : 400 // 指定图表高度
      });
      // Step 2: 载入数据源
      chart.source(data, {
        genre: {
          alias: '日期',
        },
        sold: {
          alias: '数量'
        }
      });
      // Step 3：创建图形语法，绘制柱状图，由 genre 和 sold 两个属性决定图形位置，genre 映射至 x 轴，sold 映射至 y 轴
      chart.line().position('genre*sold')
      // Step 4: 渲染图表
      chart.render();

      chart1.source(data);
      // Step 3：创建图形语法，绘制柱状图，由 genre 和 sold 两个属性决定图形位置，genre 映射至 x 轴，sold 映射至 y 轴
      chart1.interval().position('genre*sold').color('genre')
      // Step 4: 渲染图表
      chart1.render();
  }
}
</script>

<style lang='scss' scoped>
.data {
  &-chart1 {
    margin-top: 20px;
    border-radius: 5px;
    background-color: #fff;
    border: 1px solid #f5f5f5;
    &-title {
      line-height: 100px;
      padding-left: 50px;
      font-weight: 700;
      font-size: 24px;
    }
  }
  &-info {
    margin-top: 20px;
    &-box {
      .box {
        background-color: #fff;
        text-align: center;
        padding: 20px;
        border: 1px solid #f5f5f5;
        border-radius: 5px;
        .number {
          font-size: 26px;
          font-weight: 700;
          line-height: 50px;
        }
        .desc {
          font-size: 14px;
          // color:#bfc5ce;
          line-height: 40px;
        }
      }
    }
  }
}
@mixin bg($val1,$val2) {
  background: $val1; /* 一些不支持背景渐变的浏览器 */  
  background: -webkit-linear-gradient(to right,$val1, $val2); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(left,$val1, $val2); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(left,$val1, $val2); /* Firefox 3.6 - 15 */
  background: linear-gradient(left,$val1, $val2); /* 标准的语法 */
}
.box1 {
  @include bg(#DCE35B,#45B649);
  .number {
    color:#fff;
  }
  .desc {
    color:#fff;
  }
}
.box3 {
  @include bg(#4568DC,#B06AB3);
  .number {
    color:#fff;
  }
  .desc {
    color:#fff;
  }
}
</style>
