import axios from 'axios';
import ElementUI from 'element-ui';

const ip = 'http://127.0.0.1:7001/api';

// axios.defaults.withCredentials  = false;
const Axios = axios.create({
  baseURL: ip,
  timeout: 5000,
  responseType: 'json',
  withCredentials: true,
  header: {
    'Content-Type':'application/x-www-form-urlencoded;charset=utf-8'
  }
})
// console.log(Axios)
// const Axios = axios.

// 设置axios请求参数序列化
Axios.interceptors.request.use(
  config => {
    // if(sessionStorage.getItem('csrfToken')) {
    //   config.headers = {'csrfToken':sessionStorage.getItem('csrfToken')};
    // }
    // config.withCredentials = true;
    return config;
  },
  error => {
    return Promise.reject(error);
  }
)

Axios.interceptors.response.use(
  response => {
    if(response.data.respcode == 100) {
      return response;
    }else {
      ElementUI.Message({
        message: response.data.respmsg,
        type: 'warning'
      })
      return false;
    }
  }
)

// get请求
function get(url) {
  return body => Axios.get(url,{ params: body });
}

// post请求
function post(url) {
  return body => Axios.post(url, body);
}

export const getUserList = get('/v1/admin/getUserList'); // 获取用户列表
export const getUserInfo = get('/v1/admin/getUserInfo'); // 获取用户详细信息
export const changeUserStatus = post('/v1/admin/changeUserStatus'); // 更改用户状态
export const removeUser = post('/v1/admin/removeUser'); // 删除用户
export const adminLogin = post('/v1/admin/login'); // 管理登录