<template>
  <div class="add-admin">
    <transition name="slideDown">
      <div class="add-admin-box" v-if='show'>
        <h4>
          <span>查看 / 编辑用户信息</span>
          <i class='el-icon-close' @click='close'></i>
        </h4>
        <!-- 表单 -->
        <div class="add-admin-form">
          <el-form size='mini' status-icon ref="ruleForm2" label-width="70px">
            <el-form-item label="用户ID" prop="pass">
              <span>{{ userInfo.id }}</span>
            </el-form-item>
            <el-form-item label="用户身份" prop="pass">
              <span>{{ type[userInfo.type] }}</span>
            </el-form-item>
            <el-form-item label="注册时间" prop="pass">
              <span>{{ getDate(userInfo.meta.regTime) || '无' }}</span>
            </el-form-item>
            <el-form-item label="上次登录" prop="pass">
              <span>{{ userInfo.meta.prevLogTime ? getDate(userInfo.meta.prevLogTime) : '无' }}</span>
            </el-form-item>
            <el-form-item label="手机号" prop="pass">
              <el-input v-model="phone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="用户密码" prop="age">
              <el-row>
                <el-col :span='20' class='pwd'>
                  <span>****************</span>
                  <b>点击重置密码将把密码改为 bibao123456</b>
                </el-col>
                <el-col :span='4'>
                  <el-button type='danger' style='width:100%'>重置密码</el-button>
                </el-col>
              </el-row>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm2')">确认修改</el-button>
            </el-form-item>
          </el-form>
        </div>

      </div>
    </transition>
  </div>
</template>

<script>
import { getUserInfo } from '@/js/http';

export default {
  mounted() {
    this.show = true;
    this.getUserInfo();
  },
  props: [ 'userId' ],
  data() {
    return {
      show: false,
      type: ['普通用户','管理员'],
      phone: null,
      userInfo: {},
    }
  },
  methods: {
    close() { // emit关闭方法

      this.$emit('close',false)

    },
    getDate(str) { // 格式化日期

      let date = new Date(str);
      date = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`;
      return date;

    },
    async getUserInfo() { // 获取用户信息

      const res = await getUserInfo({ id: this.userId });
      this.userInfo = res.data.data;
      this.phone = res.data.data.phone;

    }
  }
}
</script>

<style lang='scss' scoped>
@import '../../views/scss/public.scss';
@import './scss/addAdmin.scss';

</style>
