import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
// 数据展示
import WebData from './views/PageData/WebData.vue'
import TranData from './views/PageData/TranData.vue'
import TokenData from './views/PageData/TokenData.vue'

// 管理员相关
import AdminList from './views/PageAdmin/AdminList.vue'
import AdminLog from './views/PageAdmin/AdminLog.vue'

// 用户管理
import UserList from './views/PageUser/UserList.vue'
import UserCheck from './views/PageUser/UserCheck.vue'
// 登录页
import Login from './views/PageLogin/Login.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect:'/webData',
      component: Home,
      children: [
        {
          path: 'webData',
          name: 'web-data',
          component: WebData,
        },
        {
          path: 'transactionData',
          name: 'transaction-data',
          component: TranData,
        },
        {
          path: 'tokenData',
          name: 'token-data',
          component: TokenData,
        },
        {
          path: 'adminlist',
          name: 'admin-list',
          component: AdminList
        },
        {
          path: 'adminlog',
          name: 'admin-log',
          component: AdminLog
        },
        {
          path: 'userlist',
          name: 'user-list',
          component: UserList
        },
        {
          path: 'usercheck',
          name: 'user-check',
          component: UserCheck
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})
