<template>
  <div class="login">
    <div class="login-inner">
      <div class="login-logo">
        <img src="../../assets/img/logo.png" alt="">
      </div>
      <el-form size='normal'>
        <el-form-item>
          <el-input v-model='phone' placeholder="请输入管理账号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model='password' type='password' placeholder="请输入用户密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type='primary' style='width:100%;' @click='adminLogin'>登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { adminLogin } from '@/js/http';

export default {
  mounted() {

  },
  data() {
    return {
      phone: '',
      password: '',
    }
  },
  methods: {
    async adminLogin() {
      try {
        const res = await adminLogin({ phone: this.phone, password: this.password });
        if(res) {
          sessionStorage.setItem('csrfToken', res.data.data.token);
          this.$message({
            message: res.data.respmsg,
            type:'success',
            duration: 2000
          });
          this.$router.push('/userlist');
        }
      }catch (e) {
        throw e;
      }
    }
  }
}
  
</script>

<style lang='scss' scoped>
  @mixin bg($val1,$val2) {
    background: $val1; /* 一些不支持背景渐变的浏览器 */  
    background: -webkit-linear-gradient(to right,$val1, $val2); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(left,$val1, $val2); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(left,$val1, $val2); /* Firefox 3.6 - 15 */
    background: linear-gradient(left,$val1, $val2); /* 标准的语法 */
  }
  .login {
    background: url(../../assets/img/bg.jpg) center;
    height: 100%;
    min-width: 1300px;
    position: relative;
    &-logo {
      padding-bottom: 50px;
      img {
        width: 100%;
      }
    }
    &-inner {
      width: 500px;
      // height: 500px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%,-50%,0)
    }
    .el-form-item {
      padding-bottom: 20px;
    }
    .el-button--primary {
      @include bg(#b4c6ff,#4f79ff);
      border: 0;
      &:hover {
        box-shadow: 0 0 20px #4f79ff;
      }
    }
  }

  
</style>
