        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
        <el-col class='data-list-token' :span='2'><a href='#'>ETH/MCNY</a></el-col>
      </el-row>
    </div>
    
    <div class="data-info">
      <el-row  :gutter='20'>
        <el-col :span='6' class='data-info-box'>
          <div class="box box1">
            <div class="number">123456</div>
            <div class="desc">今天成交量</div>
          </div>
        </el-col>
        <el-col :span='6' class='data-info-box'>
          <div class="box box2">
            <div class="number">123456</div>
            <div class="desc">累计成交量</div>
          </div>
        </el-col>
        <el-col :span='6' class='data-info-box'>
          <div class="box box3">
            <div class="number">123456</div>
