<template>
  <div class="table">
    <!-- 条件筛选 -->
    <el-row class='table-head'>
      <el-col :span='16'>
        <label>状态：</label>
        <el-select v-model="value" placeholder="请选择" size='mini'>
          <el-option
            key="1"
            label="禁用"
            value="1">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span='8' class='table-head-btn'>
        <el-button size='mini' type='success' @click='changeshow'>新增管理员</el-button>
        <!-- <el-button size='mini' type='default'>导出Exel表格</el-button> -->
      </el-col>
    </el-row>
    <!-- 列表 -->
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="type"
        label="管理身份"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="账号名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态">
      </el-table-column>
      <el-table-column
        prop="address"
        label="联系方式">
      </el-table-column>
      <el-table-column
        width='300'
        label="操作">
        <template slot-scope="scope">
          <el-button type='success' size='mini'>启用</el-button>
          <el-button type='primary' size='mini'>编辑权限</el-button>
          <el-button type='default' size='mini'>密码重置</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页器 -->
    <div class="table-page">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
    <!-- 弹出层 -->
    <transition name="fade">
      <add-admin v-if='show' @close='close'></add-admin>
    </transition>
  </div>
</template>

<script>
import AddAdmin from './AddAdmin'

export default {
  components: { AddAdmin },
  data() {
    return {
      show:false,
      value:'1',
      tableData:[
        {type:'总管理',name:'张三',status:'启用',address:'15522233332'}
      ]
    }
  },
  methods: {
    changeshow() {
      this.show=true;
    },
    close(data) {
      this.show=data;
    }
  }
}
</script>

<style lang='scss' scoped>
// .el-table th > .cell {
//   font-weight: 100 !important;
// }

.table {
  &-head {
    padding: 10px 0 30px 0;
    label {
      font-size: 14px;
      color: #999;
    }
    &-btn {
      text-align: right;
    }
  }
  &-page {
    padding-top: 30px;
  }
}
</style>
